import axios from 'axios'

const server = process.env.NODE_ENV === 'production' ? '/' : 'https://localhost:44397/'

export const AO = axios.create({
  baseURL: server + 'api'
})

export function serverUrl(part) {
  return server + part
}

export default {
  install(app) {
    app.mixin({
      methods: {
        $fetch(url, method = 'get', data, onUploadProgress = undefined, headers = undefined,
          baseURL = server + 'api') {
          if (method.toLowerCase() === 'get' && typeof data === 'object') {
            let urlEncoded = ''
            for (const k in data) {
              urlEncoded += encodeURIComponent(k) + '=' + encodeURIComponent(data[k]) + '&'
            }
            url += '?' + urlEncoded.substr(0, urlEncoded.length - 1)
          }
          return axios(url, {
            baseURL,
            method,
            data,
            onUploadProgress,
            headers
          })
        }
      }
    })
  }
}
