export default [
  {
    path: '/',
    redirect: '/website/settings'
  },
  {
    path: '/member/profile',
    component: () => import('../../../components/user/Profile'),
    meta: { title: 'پروفایل کاربر' }
  },

  {
    path: '/qna',
    component: () => import('../../../components/qna/Questions'),
    meta: { title: 'پرسش‌ها و پاسخ‌ها' }
  },
  {
    path: '/qna/new',
    component: () => import('../../../components/qna/QuestionForm'),
    meta: { title: 'ثبت پرسش جدید' }
  },
  {
    path: '/qna/:qnAId/edit',
    component: () => import('../../../components/qna/QuestionForm'),
    meta: { title: 'ویرایش پرسش' },
    props: true
  },

  //website
  {
    path: '/website/settings',
    component: () => import('../../../components/website/ThemeEditor'),
    meta: { title: 'تنظیمات وبسایت - محتوای صفحه نخست' },
    props: true
  },
  {
    path: '/website/home-slideshows',
    component: () => import('../../../components/website/HomeSlideshows'),
    meta: { title: 'تنظیمات وبسایت - اسلاید شوهای صفحه نخست' },
    props: true
  },

  {
    path: '/contact-us',
    component: () => import('../../../components/contact-us/ContactUs'),
    meta: { title: 'سوالات متداول' }
  },

  //cms
  {
    path: '/cms/categories',
    component: () => import('../../../components/cms/Categories'),
    meta: { title: 'گروه‌های محتوایی' }
  },

  {
    path: '/cms/posts',
    component: () => import('../../../components/cms/Posts'),
    meta: { title: 'مقالات' }
  },
  {
    path: '/cms/posts/new',
    component: () => import('../../../components/cms/PostForm'),
    meta: { title: 'مقاله جدید' }
  },
  {
    path: '/cms/posts/:pstId/edit',
    component: () => import('../../../components/cms/PostForm'),
    meta: { title: 'ویرایش مقاله' },
    props: true
  }
]
